/*/-_-_-_-_-_-_-_-_    Header   -_-_-_-_-_-_-_-_ /*/

.menu_nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 14px 82px;
}

/*/-_-_-_-_-_-_-_-_-_   Logo  _-_-_-_-_-_-_-_-_ /*/

.menu_img {
  object-fit: contain;
  width: 140px;
}

/*/-_-_-_-_-_-_   Menu Hamburguer  -_-_-_-_-_-_ /*/

.menu_hamburger {
  border: 0;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  right: 20px;
}

@media (min-width: 980px) {
  .menu_hamburger {
    display: none;
  }
}

@media (max-width: 980px) {
  .menu_hamburger {
    display: flex;
  }
}

/*/-_-_-_-_-_-_-_-_-_   Menu  _-_-_-_-_-_-_-_-_ /*/

@media (min-width: 980px) {
  .menu {
    width: 100%;
    height: 100%;
    background: none;
    flex-direction: row;
    padding: 0;
    align-items: flex-start;
    justify-content: space-between;
  }
}

@media (max-width: 980px) {
  .menu {
    display: none;
  }
}

/* - _ - _ - _ - _ - _ - _ - _ - _ - _ - _ - _ - _ */

.menu_list {
  display: flex;
  list-style: none;
  justify-content: space-between;
}

/* - _ - _ - _ - _ - _ - _ - _ - _ - _ - _ - _ - _ */

.menu_list_item {
  color: var(--secondary-text-color);
}

/* - _ - _ - _ - _ - _ - _ - _ - _ - _ - _ - _ - _ */

.menu_list_link {
  display: flex;
  text-decoration: none;
  color: var(--secondary-text-color);
  text-transform: uppercase;
  font-weight: 300;
  text-align: center;
  font-size: 14px;
}

.menu_list_link:hover {
  color: var(--secondary-text-color);
  border-bottom: 2px solid var(--highlight-color);
}

.container_hero {
  width: 100%;
  height: 87vh;
  background: url(/micropigmentacao-solidaria/static/media/hero_max.e5a868cb.png) center center/cover
    no-repeat fixed padding-box rgba(67, 3, 7, 0.4);
  text-align: left;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.title_hero {
  width: 100%;
  max-width: 350px;
  font-family: 'Nunito';
  font-size: 64px;
  font-weight: bold;
  margin: 10px 0px;
}


.content_hero{
  margin: 60px 60px;
}

.text_hero {
  margin: 30px 0px;
  width: 80%;
  max-width: 600px;
  font-family: 'Open Sans';
  font-size: 24px;
}

.link_hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0px;
}

.command_hero {
  font-family: 'Nunito';
  font-size: 14px;
  color: #ffffff;
  text-decoration: none;
  margin: 0px 0px;
}

.command_hero,
.anchor_hero,
.icon_hero {
  margin: 0px 0px;
}

.about {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 50px 0px;
  width: 100%;
}

@media (min-width: 980px) {
  .about {
    flex-direction: row;
    margin: 110px 0px;
  }
}

.about__text {
  text-align: left;
  width: 100%;
}

@media (min-width: 980px) {
  .about__text {
    padding: 90px 50px 40px 100px;
    width: 40%;
  }
}

.about__text__title {
  font-family: var(--primary-font);
  font-size: var(--size-h2);
  color: var(--highlight-color);
  text-align: center;
  padding: 0 20px;
}

@media (min-width: 980px) {
  .about__text__title {
    padding: 0;
    margin: 0 0 10px 0;
    text-align: left;
  }
}

.about__text__paragraph {
  font-family: var(---secondary-font);
  font-size: var(--size-p);
  color: var(--primary-text-color);
  text-align: justify;
  line-height: 2;
