  .about__text__paragraph {
    padding: 0 100px;
  }
}

@media (min-width: 980px) {
  .about__text__paragraph {
    padding: 0;
    margin: 30px 0 0 0;
  }
}

.about__image {
  background: var(--highlight-color);
  width: 100%;
  height: auto;
  padding: 70px;
  margin: 0;
  display: flex;
}

@media (min-width: 680px) {
  .about__image {
    height: 470px;
    padding: 100px;
  }
}

@media (min-width: 980px) {
  .about__image {
    padding: 70px 0 70px 100px;
    width: 60%;
  }
}

.about__image__src {
  width: 100%;
  margin: 0px;
  object-fit: contain;
}

.whoDoes {
  background: var(--color-background);
  display: flex;
