  }
}

.whoDoes__info__title {
  color: var(--highlight-color);
  font-family: var(--primary-font);
  font-size: var(--size-h3);
  margin: 20px 0;
}

@media (min-width: 1020px) {
  .whoDoes__info__title {
    margin: 0 0 20px 0;
    font-size: var(--size-h2);
  }
}

.whoDoes__info__subtitle {
  font-family: var(--primary-font);
