}

.whoDoes__info__description {
  font-family: var(--secondary-font);
  font-size: var(--secondary-font);
  color: var(--primary-text-color);
  text-align: justify;
  margin-top: 20px;
  line-height: 2;
}

.whoDoes__team {
  display: flex;
  align-items: center;
  padding: 60px 0;

  max-width: 1190px;
  width: 100%;
}

.whoDoes__team-figure {
  width: 165px;
  margin: 5px;
}

@media (min-width: 1020px) {
  .whoDoes__team-figure {
    margin: 5px 30px 5px 0;
  }
}

.whoDoes__icons {
  width: 50%;
  display: flex;
  align-items: baseline;
  justify-content: space-between;
}

@media (min-width: 1020px) {
  .whoDoes__icons {
    width: 150px;
  }
}

