@font-face{
  font-family: Nunito-Bold;
  src: url(/micropigmentacao-solidaria/static/media/Nunito-Bold.1cd294a7.ttf);
}

@font-face{
  font-family: Nunito-SemiBold;
  src: url(/micropigmentacao-solidaria/static/media/Nunito-SemiBold.76ae20d4.ttf);
}

:root {
  --color-background: rgba(142, 103, 105, 0.2);
  --color-1: #430307;
  --color-2: #8E6769;
  --size-h2: 24px;
  --size-p: 16px;
  --font-p: 'Open Sans', sans-serif;
}

.faq {
  background: rgba(142, 103, 105, 0.2);
  background: var(--color-background);
  flex-direction: column;
  align-items: center;
  display: flex;
  width: 100%;
  
}

.faq_header {
  font-family: Nunito;
  text-transform: uppercase;
  font-style: normal;
  font-size: 2.125em;
  text-align: center;
  color: #430307;
  color: var(--color-1);
  margin-top: 38px;
}

.faq_list {
  padding: 0;
  margin: 0;
  list-style-type:none;
  padding-bottom: 7rem;
  max-width: 60em;

}

@media(min-width:943px) {
  .faq_list{
    display:flex;
    flex-wrap: wrap;
  }
}


.faq_item  {
  border: solid 0.1px #C2D1D9;
  background: #FFFFFF;
  width: 100%;
  margin: 0;
}

.faq_details {
  text-align: left;
}

.faq_question {
  text-transform: uppercase;
  font-family: Nunito-SemiBold;
  font-size: 16px;
  font-size: var(--size-p);
  font-weight: 600;
  color: #430307;
  color: var(--color-1);
  padding: 30px; 
  outline: none;
}

.faq_answer {
  white-space: pre-line;
  font-family: 'Open Sans', sans-serif;
  font-family: var(--font-p);
  color: #8E6769;
  color: var( --color-2);
  border: solid 0.1px #C2D1D9;
  line-height: 25px;
  text-align: justify;
  text-indent:4em;
  padding: 30px;
}

summary::-webkit-details-marker {
  display: none
}
summary:after {
  background: #430307;
  background: var(--color-1);
  content: "+"; 
  color: #fff; 
  float: right;  
  text-align: center; 
  font-size: x-large;
  font-weight: bold; 
  margin: 0;
  padding: 0; 
  width: 26px;
}
details[open] summary:after {
  content: "-";
  float: right;  
  font-size: x-large;
  font-weight: bold; 
  text-align: center; 
  width: 26px;
}
.footer {
  display: flex;
  flex-direction: column;
}

.footer__container {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  padding: 30px 0 0 0;
}

@media (min-width: 980px) {
  .footer__container {
    flex-direction: row;
    align-items: flex-start;
    padding: 30px 82px;
  }
}

.footer__list-group {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 650px;
  padding: 0 0 30px 0;
  justify-content: space-between;
}

@media (min-width: 980px) {
  .footer__list-group {
    flex-direction: row;
    align-items: flex-start;
    padding: 0;
  }
}

.footer__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  list-style: none;
  margin: 0 0 30px 0;
  padding: 0;
}

@media (min-width: 980px) {
  .footer__list {
    align-items: flex-start;
    margin: 0;
  }
}

.footer__figure__logo {
  margin: 0;
}

.footer__logo {
  width: 97px;
  height: 50px;
}

.footer__social-media {
  display: flex;
}

.footer__title {
  color: var(--secondary-text-color);
  font-family: var(--primary-font);
  font-size: var(--size-title);
  text-decoration: none;
  font-weight: bold;
  display: flex;
  padding-bottom: 5px;
}

.footer__link {
  color: var(--primary-text-color);
  font-family: var(--font-item);
  font-size: var(--size-item);
  text-decoration: none;
  display: flex;
  padding-bottom: 5px;
}

.footer__copyright {
  color: var(--primary-text-color);
  width: 100%;
  border-top: 1px solid var(--highlight-color);
  padding: 14px 82px;
  margin: 0;
  text-align: center;
}

@media (min-width: 980px) {
  .footer__copyright {
    text-align: right;
  }
}

* {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  width: 100%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --highlight-color: #430307;
  --primary-text-color: #8e6769;
  --secondary-text-color: #b76882;
  --color-background: #f2f2f2;

  --primary-font: 'Nunito', sans-serif;
  --secondary-font: 'Open Sans', sans-serif;

  --size-title: 14px;
  --size-item: 14px;
  --size-h2: 50px;
  --size-p: 16px;
  --size-h3: 28px;
  --size-h4: 21px;
  --size-p: 16px;
}

